import { ChangeEventHandler, CSSProperties, LegacyRef, MouseEventHandler, useState } from 'react';
import { ReactComponent as EyeIcon } from '../../assets/images/eye.svg';
import styles from './inputPasswordWithEye.component.module.css';

interface InputPasswordWithEyeProps {
  placeholder?: string;
  inputRef?: LegacyRef<HTMLInputElement>;
  value: string;
  onClick?: MouseEventHandler<HTMLInputElement>;
  onChange?: ChangeEventHandler<HTMLInputElement>;
  style?: CSSProperties;
}
export const InputPasswordWithEye = ({
  placeholder,
  style,
  inputRef,
  value,
  onClick,
  onChange,
}: InputPasswordWithEyeProps) => {
  const [showPassword, setShowPassword] = useState(false);

  const togglePasswordView = () => {
    setShowPassword(!showPassword);
    console.log(showPassword);
  };

  return (
    <div style={{ border: '1px solid var(--gray)', borderRadius: '0.375rem', width: '100%' }}>
      <input
        className={styles.inputPasswordField}
        type={showPassword ? 'text' : 'password'}
        placeholder={placeholder}
        style={{ ...style, border: 'none' }}
        ref={inputRef}
        value={value}
        onClick={onClick}
        onChange={onChange ? onChange : () => {}}
      />
      <EyeIcon className="my-auto py-1" onClick={togglePasswordView} style={{ width: '10%' }} />
    </div>
  );
};
