import iTooltip from 'assets/images/i_tooltip.svg';
import threeDots from 'assets/images/three_dots.svg';
import { ChildrenMachine } from 'components/ChildrenMachine/ChildrenMachine';
import { OfflineAlert } from 'components/Alerts/OfflineAlert';
import { useAsync } from 'hooks/useAsync';
import { Button, Col, Container, Row } from 'react-bootstrap';
import { useNavigate, useParams } from 'react-router-dom';
import { routsConstant } from 'routes/routes.const';
import { cotService } from 'services/cot.service';
import { GWSetupProgressBar } from '../components/GWSetupProgressBar/GWSetupProgressBar';
import { ConnectionType } from '../types/GWSetupWizard.types';
import { NearbyMachines } from 'components/NearbyMachines/NearbyMachines';
import { AddNearbyMachine } from 'components/AddNearbyMachine/AddNearbyMachine';

const serialPortFeatureFlag = false;

const machines = [
  {
    name: 'machineA',
    serialNo: '1234456',
    model: 'modelA',
    connectionType: ConnectionType.serialPort,
    portNo: 111,
  },
  {
    name: 'machineB',
    serialNo: '88866555',
    model: 'modelB',
    connectionType: ConnectionType.serialPortConnection,
    portNo: 333,
  },
  {
    name: 'machineC',
    serialNo: '34234232',
    model: 'modelC',
    connectionType: ConnectionType.lanConnection,
  },
];

const getMachines = (machines: any[], connectionType: ConnectionType) => {
  let filteredMachines = machines.filter((machine) => machine.connectionType === connectionType);
  const machineRows = filteredMachines.map((machine) => (
    <Row className="h-70 mb-2">
      <Col className="d-flex align-items-center">
        <div className="machine-icon"></div>
        <div className="d-flex flex-column flex-grow-1">
          <div className="setup-section-title">{machine.name}</div>
          <div className="setup-section-subtitle fs-14">
            {machine.serialNo}&nbsp;|&nbsp;{machine.model}
          </div>
        </div>
        {machine.connectionType === ConnectionType.serialPort ? (
          <div className="d-flex">
            <div className="fs-21">Port {machine.portNo}</div>
            <div>
              <Button variant="link">
                <img alt="action" src={threeDots} />
              </Button>
            </div>
          </div>
        ) : null}
      </Col>
    </Row>
  ));

  const availableSerialPorts = [{ port: 111 }, { port: 222 }];
  if (connectionType === ConnectionType.serialPort && machineRows.length < availableSerialPorts.length) {
    const usedPorts = filteredMachines.map((machine) => machine.portNo);
    availableSerialPorts
      .filter((serialPort) => !usedPorts.includes(serialPort.port))
      .forEach((serialPort) => {
        machineRows.push(
          <Row className="h-70 mb-2">
            <Col className="d-flex align-items-center">
              <Button className="outlined-btn flex-grow-1">Assign Port {serialPort.port}</Button>
            </Col>
          </Row>
        );
      });
  }
  return machineRows;
};

const SerialPortConfig = () => {
  if (!serialPortFeatureFlag) {
    return <></>;
  }
  return (
    <>
      <Row>
        <Col className="d-flex mt-4 mb-2 align-items-center">
          <div className="setup-section-title underline flex-grow-1">Serial Ports</div>
          <Button variant="link">
            <img alt="action" src={iTooltip} />
          </Button>
        </Col>
      </Row>
      {getMachines(machines, ConnectionType.serialPort)}
    </>
  );
};

export const GWSetupSetMachines = () => {
  const { companyId, projectId, deviceId } = useParams();
  const { value: deviceChildren } = useAsync(() => cotService.getDeviceChildren(deviceId!), { interval: 5000 });
  const navigate = useNavigate();

  return (
    <>
      <GWSetupProgressBar currentStep={'Machines'} />
      <OfflineAlert deviceId={deviceId!} projectId={projectId!} />
      <Container>
        <Row>
          <Col className="setup-step-title mb-4">Add &amp; Remove Machines</Col>
        </Row>
        <SerialPortConfig />
        <Row>
          <Col className="d-flex mt-4 mb-2 align-items-center">
            <div className="setup-section-title underline flex-grow-1">Lan Connections</div>
            <Button variant="link">
              <img alt="action" src={iTooltip} />
            </Button>
          </Col>
        </Row>
        <ChildrenMachine deviceChildren={deviceChildren}></ChildrenMachine>
        <NearbyMachines
          companyId={companyId!}
          projectId={projectId!}
          deviceIdsToFilter={deviceChildren?.map((device) => device.id) ?? []}
        />
        <AddNearbyMachine companyId={companyId!} projectId={projectId!} />
        <Row className="mt-5 mb-5 text-center">
          <Col>
            <Button
              className="filled-btn"
              onClick={() =>
                navigate(
                  routsConstant.gwSetupCompleted.relatirve({
                    companyId: companyId!,
                    projectId: projectId!,
                    deviceId: deviceId!,
                  })
                )
              }
            >
              Confirm
            </Button>
          </Col>
        </Row>
      </Container>
    </>
  );
};
