import { Col, Form, Row } from 'react-bootstrap';
import { GwLocation } from 'types/cot.properties.types';

export const GwLocationForm = ({ deviceId, gwLocations }: { deviceId: string; gwLocations: GwLocation[] }) => {
  let gwLocation = gwLocations.find((location) => location.deviceId === deviceId);
  if (!gwLocation) {
    gwLocation = { deviceId, floor: '', room: '' };
    gwLocations.push(gwLocation);
  }
  return (
    <>
      <Row>
        <Col>
          <Form.Label className="setup-section-title mt-4">Where is the gateway installed?</Form.Label>
        </Col>
      </Row>
      <Row>
        <Col>
          <Form.Group className="mb-2" controlId="gwFloor">
            <Form.Control
              className="setup-form-field-text"
              type="text"
              placeholder="Floor"
              defaultValue={gwLocation.floor}
              onChange={(e) => (gwLocation!.floor = e.target.value)}
            />
          </Form.Group>
        </Col>
        <Col>
          <Form.Group className="mb-2" controlId="gwRoom">
            <Form.Control
              className="setup-form-field-text"
              type="text"
              placeholder="Room"
              defaultValue={gwLocation.room}
              onChange={(e) => (gwLocation!.room = e.target.value)}
            />
          </Form.Group>
        </Col>
      </Row>
    </>
  );
};
