import { StepperItem } from 'components/Stepper/Stepper';
import './GWSetupProgressBar.css';

interface ProgressBarProps {
  steps?: StepperItem[];
  theme?: 'light' | 'dark';
  size?: 'normal' | 'large';
  currentStep?: string;
  completedStep?: string;
  allCompleted?: boolean;
}

const stepperItems: StepperItem[] = [
  {
    name: 'Identification',
  },
  {
    name: 'Clinic',
  },
  {
    name: 'Location',
  },
  {
    name: 'Machines',
  },
];

export const GWSetupProgressBar: React.FC<ProgressBarProps> = ({
  theme = 'dark',
  size = 'normal',
  currentStep,
  steps = stepperItems,
  completedStep,
  allCompleted = false,
}: ProgressBarProps) => {
  const getClassName = (stepName: string) => {
    const classes: string[] = [theme, size];
    const stepNames = steps.map((step) => step.name);
    if (currentStep === stepName) {
      classes.push('current-stage');
    }
    if ((currentStep && stepNames.indexOf(currentStep) > stepNames.indexOf(stepName)) || stepName === completedStep || allCompleted) {
      classes.push('completed-stage');
    }

    return classes.join(' ');
  };

  const getStepWidth = () => {
    return steps && steps.length > 0 ? `${Math.floor(100 / steps.length)}%` : '100%';
  };

  return (
    <div className={`pb-container ${theme} ${size}`}>
      <ul className={`progressbar ${theme} ${size}`}>
        {steps?.map((stepperItem, index) => (
          <li
            key={stepperItem.name}
            className={getClassName(stepperItem.name)}
            step-ind={index + 1}
            style={{ width: getStepWidth() }}
          >
            {stepperItem.name}
          </li>
        ))}
      </ul>
    </div>
  );
};
