import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';
import { API_MAP, request } from 'services/httpService';
import { RootState } from '../store';

export interface COTUserResponse {
  apikey: string;
  company: string;
  role: string;
  verified: number;
}

export interface UserState {
  email: string;
  apiKey: string;
  company: string;
  role: string;
}

let user: UserState = {
  email: '',
  apiKey: '',
  company: '',
  role: '',
};

const storedUser = localStorage.getItem('userDetails');
if (storedUser) {
  user = { user, ...JSON.parse(storedUser) };
}

const initialState: UserState = user;

type LoginTheUserParam = {
  userName: string;
  password: string;
};

export const loginTheUser = createAsyncThunk<COTUserResponse, LoginTheUserParam, { state: RootState }>(
  'user/login',
  async (param: LoginTheUserParam) => {
    return (
      await request(API_MAP.login, {
        payload: {
          email: param.userName,
          password: param.password,
        },
      })
    );
  }
);

export const userSlice = createSlice({
  initialState,
  name: 'user',
  reducers: {
    setEmailAction: (state, action: PayloadAction<string>) => {
      state.email = action.payload;
    },
    logoutTheUser: (state, action: PayloadAction<void>) => {
      const newState = { apiKey: '', company: '', email: '', role: '' };
      localStorage.setItem('userDetails', JSON.stringify({ ...newState }));
      return newState;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(loginTheUser.pending, (state) => {
        console.log('login pending');
      })
      .addCase(loginTheUser.fulfilled, (state, action) => {
        const cotUserResponse = action.payload;
        if (cotUserResponse) {
          state.apiKey = cotUserResponse.apikey;
          state.company = cotUserResponse.company;
          state.role = cotUserResponse.role;
          state.email = action.meta.arg.userName;
          localStorage.setItem('userDetails', JSON.stringify({ ...state }));
        }
        console.log('login fulfilled');
      })
      .addCase(loginTheUser.rejected, (state) => {
        console.log('login rejected');
      });
  },
});

export const { setEmailAction, logoutTheUser } = userSlice.actions;
export const selectUser = (state: RootState) => state.user;
export default userSlice.reducer;
