import './InputText.css';

type InputTextProps = {
  value?: string;
  icon?: string;
  placeholder?: string;
  onChange?: React.ChangeEventHandler<HTMLInputElement>;
  ref?: any;
};

export const InputText = ({ value, icon, placeholder, onChange, ref }: InputTextProps) => {
  return (
    <div className="input_wrapper">
      <input
        style={{ background: icon }}
        className="input"
        onChange={onChange}
        value={value}
        placeholder={placeholder}
        ref={ref}
      />
      {icon ? <img className="icon" src={icon} alt=""></img> : <></>}
    </div>
  );
};
