import { WifiNotConnectedAlert } from 'components/Alerts/WifiNotConnectedAlert';
import { NowBrandingLayout } from 'layouts/NowBrandingLayout';
import { GWDashboard } from 'pages/GWDashboard/GWDashboard';
import {
  GWSetupClinicSearch,
  GWSetupSetLocation,
  GWSetupNewClinic,
  GWSetupStart,
  GWSetupLocationDetails,
  GWSetupMachinesRecommendation,
  GWSetupSetMachines,
  GWSetupCompleted,
} from 'pages/GWSetupWizard';
import { GWSearchGwBySerial } from 'pages/GWSetupWizard/GwSearchGwBySerial/GWSearchGwBySerial';
import { GearIcon } from 'pages/NavBar/components/GearIcon/GearIcon';
import { UserIcon } from 'pages/NavBar/components/UserIcon/UserIcon';
import { WifiSignalIcon } from 'pages/NavBar/components/WifiSignalIcon/WifiSignalIcon';
import { Navigate, Route, Routes } from 'react-router-dom';
import { WifiProvider } from '../components/WifiSelectModal/WifiContext';
import { Login } from '../pages/Login/Login';
import { OperatorGuide } from '../pages/OperatorGuide/OperatorGuide';
import { PrivateRoute } from './PrivateRoute';
import { routsConstant } from './routes.const';
import { MenuItem } from './routes.types';
import { ScrollToTop } from './ScrollToTop';

const webIconProvider = (): Array<JSX.Element | undefined> => {
  return [UserIcon(), GearIcon([])];
};

const boardIconProvider = (): Array<JSX.Element | undefined> => {
  const menuItems: MenuItem[] = [
    {
      title: 'WiFi',
      to: routsConstant.wifiConfig.absolute(),
    },
    {
      title: 'Barcode to allocate',
      to: routsConstant.allocateQrCode.absolute(),
    },
  ];
  return [WifiSignalIcon(), GearIcon(menuItems)];
};

export const AppRoute = () => {
  return (
    <ScrollToTop>
      <Routes>
        <Route
          path="/"
          element={
            <NowBrandingLayout iconsProvider={boardIconProvider}>
              <WifiNotConnectedAlert></WifiNotConnectedAlert>
            </NowBrandingLayout>
          }
        >
          <Route path="/" element={<Navigate to={routsConstant.allocateQrCode.path()} replace />} />
          <Route
            path={routsConstant.allocateQrCode.path()}
            element={<OperatorGuide qrUrl="/api/gw/qr/homepage" description="qrHomepageDecription" />}
          />
          <Route path={routsConstant.wifiConfig.path()} element={<WifiProvider />} />
        </Route>
        <Route path="login" element={<Login />} />

        <Route path="/" element={<PrivateRoute />}>
          <Route path="/" element={<NowBrandingLayout iconsProvider={webIconProvider} />}>
            <Route path={routsConstant.gwDashboard.path()} element={<GWDashboard />} />
          </Route>

          <Route path="setup" element={<NowBrandingLayout iconsProvider={webIconProvider} />}>
            <Route path={routsConstant.gwSearchGwBySerial.path()} element={<GWSearchGwBySerial />} />
            <Route path={routsConstant.gwSetupStart.path()} element={<GWSetupStart />} />
            <Route path={routsConstant.gwSetupClinicSearch.path()} element={<GWSetupClinicSearch />} />
            <Route path={routsConstant.gwSetupNewClinic.path()} element={<GWSetupNewClinic />} />
            <Route path={routsConstant.gwSetupSetLocation.path()} element={<GWSetupSetLocation />} />
            <Route path={routsConstant.gwSetupLocationDetails.path()} element={<GWSetupLocationDetails />} />
            <Route
              path={routsConstant.gwSetupMachinesRecommendation.path()}
              element={<GWSetupMachinesRecommendation />}
            />
            <Route path={routsConstant.gwSetupSetMachines.path()} element={<GWSetupSetMachines />} />
            <Route path={routsConstant.gwSetupCompleted.path()} element={<GWSetupCompleted />} />
          </Route>
        </Route>
      </Routes>
    </ScrollToTop>
  );
};
