import { useAsync } from 'hooks/useAsync';
import { useLoadIcon } from 'hooks/useLoadIcon';
import { gwService } from 'services/gw.service';

export const WifiSignalIcon = () => {
  const { value: wifiList } = useAsync(() => gwService.getWifiList(), { interval: 5000 });
  const signal = wifiList?.find((wifi) => wifi.connected)?.signal ?? 0;
  const signalIconName = signal === 0 ? 'no-wifi-signal.svg' : `wifi-signal-${Math.floor(signal / 25)}.svg`;
  const signalIcon = useLoadIcon(signalIconName);
  return <img src={signalIcon} alt=""></img>;
};
