import addressMarker from 'assets/images/address-marker.svg';
import { Button, Col, Container, Form, InputGroup, Row } from 'react-bootstrap';
import { GWSetupProgressBar } from '../components/GWSetupProgressBar/GWSetupProgressBar';
import { ContactPerson } from 'components/ContactPerson/ContactPerson';
import { useShowPopup } from 'hooks/useShowPopup';
import { useSpinner } from 'hooks/useSpinner';
import { concatenateGeolocation, getGeolocation } from 'utils/getGeolocation';
import { useRef } from 'react';
import { useForceUpdate } from 'hooks/useForceUpdate';
import { GwLocation, projectContactInitValue, ProjectMetadata } from 'types/cot.properties.types';
import { cotService } from 'services/cot.service';
import { useNavigate, useParams } from 'react-router';
import { routsConstant } from 'routes/routes.const';
import { AddNotes } from './AddNotes';
import { AddWorkingHours } from './AddWorkingHours';
import { createSearchParams, useSearchParams } from 'react-router-dom';
import { GwLocationForm } from 'components/GwLocationForm/GwLocationForm';
import { CompanySelect } from './CompanySelect';

const SetGeolocation = ({ projectMetadata }: { projectMetadata: React.MutableRefObject<ProjectMetadata> }) => {
  const showPopup = useShowPopup();
  const showSpinner = useSpinner();
  const forceUpdate = useForceUpdate();

  const setGeolocation = async () => {
    try {
      showSpinner(true);
      const location = await getGeolocation();
      const { latitude, longitude } = location.coords;
      projectMetadata.current.address!.geolocation = { latitude, longitude };
      forceUpdate();
      showPopup('Location set');
    } catch (error) {
      showPopup('Location set failed');
    } finally {
      showSpinner(false);
    }
  };
  return (
    <Row>
      <Col>
        <Form.Group className="mb-2" controlId="clinicGeolocation">
          <InputGroup>
            <Form.Control
              className="setup-form-field-text"
              type="text"
              placeholder="Geolocation"
              disabled
              defaultValue={concatenateGeolocation(projectMetadata.current.address?.geolocation)}
            />
            <InputGroup.Text className="address-marker" onClick={setGeolocation}>
              <img alt="address" src={addressMarker} />
            </InputGroup.Text>
          </InputGroup>
        </Form.Group>
      </Col>
    </Row>
  );
};

export const GWSetupNewClinic = () => {
  const [searchParams] = useSearchParams();
  const showPopup = useShowPopup();
  const showSpinner = useSpinner();
  const navigate = useNavigate();
  const { deviceId } = useParams();

  const projectName = useRef('');
  const selectedCompanyId = useRef('');
  const projectMetadata = useRef<ProjectMetadata>({
    contacts: [],
    address: { line1: '', line2: '' },
    contact: projectContactInitValue(),
  });

  const gwLocations = useRef<GwLocation[]>([{ deviceId: deviceId!, floor: '', room: '' }]);

  const createNewProject = async () => {
    try {
      showSpinner(true);
      const companyId = selectedCompanyId.current;
      const project = await cotService.createProject(companyId, projectName.current);
      await Promise.all([
        await cotService.setProjectProperty<ProjectMetadata>(
          project.company,
          project.id,
          'meta.data',
          projectMetadata.current
        ),
        await cotService.setProjectProperty<GwLocation[]>(
          project.company,
          project.id,
          'gw.location',
          gwLocations.current
        ),
      ]);
      navigate({
        pathname: routsConstant.gwSetupLocationDetails.relatirve({
          companyId: companyId,
          projectId: project.id,
          deviceId: deviceId!,
        }),
        search: createSearchParams(searchParams).toString(),
      });
    } catch (error) {
      console.error(error);
      showPopup('Project creation failed');
    } finally {
      showSpinner(false);
    }
  };

  return (
    <>
      <GWSetupProgressBar currentStep={'Clinic'} />
      <Container>
        <Form>
          <Row>
            <Col className="setup-step-title mb-4">Create New Clinic</Col>
          </Row>
          <CompanySelect selectedCompanyId={selectedCompanyId}></CompanySelect>
          <Row>
            <Col className="setup-section-title">Clinic Info</Col>
          </Row>
          <Row>
            <Col>
              <Form.Group className="mb-2" controlId="clinicName">
                <Form.Control
                  className="setup-form-field-text"
                  type="text"
                  placeholder="Name*"
                  onChange={(e) => (projectName.current = e.target.value)}
                />
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Group className="mb-2" controlId="clinicAddress">
                <InputGroup>
                  <Form.Control
                    className="setup-form-field-text"
                    type="text"
                    placeholder="Address line 1*"
                    onChange={(e) => {
                      projectMetadata.current.address!.line1 = e.target.value;
                    }}
                  />
                </InputGroup>
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Group className="mb-2" controlId="clinicAddress">
                <InputGroup>
                  <Form.Control
                    className="setup-form-field-text"
                    type="text"
                    placeholder="Address line 2"
                    onChange={(e) => {
                      projectMetadata.current.address!.line2 = e.target.value;
                    }}
                  />
                </InputGroup>
              </Form.Group>
            </Col>
          </Row>
          <SetGeolocation projectMetadata={projectMetadata}></SetGeolocation>
          <Row>
            <Col>
              <Form.Group className="mb-2" controlId="clinicFloor">
                <Form.Control className="setup-form-field-text" type="text" placeholder="Floor*" />
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Group>
                <AddNotes projectMetadata={projectMetadata.current} />
                <AddWorkingHours projectMetadata={projectMetadata.current} />
              </Form.Group>
            </Col>
          </Row>
          <GwLocationForm deviceId={deviceId!} gwLocations={gwLocations.current} />
          <ContactPerson contact={projectMetadata.current.contact!}></ContactPerson>
          <Row className="mt-4 mb-5 text-center">
            <Col>
              <Button
                className="outlined-btn me-3"
                type="button"
                onClick={() =>
                  navigate({
                    pathname: routsConstant.gwSetupClinicSearch.relatirve({
                      deviceId: deviceId!,
                    }),
                    search: createSearchParams(searchParams).toString(),
                  })
                }
              >
                Discard
              </Button>
              <Button className="filled-btn" onClick={createNewProject}>
                Save
              </Button>
            </Col>
          </Row>
        </Form>
      </Container>
    </>
  );
};
