import { Button, Container } from 'react-bootstrap';
import { useAppDispatch, useAppSelector } from '../../hooks/useApp';
import { loginTheUser, selectUser } from '../../store/slices/userSlice';
import { useRef } from 'react';
import { Navigate, useLocation } from 'react-router';

const useRedirect = () => {
  const user = useAppSelector(selectUser);
  const location = useLocation();
  const from: string = location.state?.from?.pathname || '/';
  return { shouldNavigate: user.apiKey.length > 0, from };
};

export const Login = () => {
  const { shouldNavigate, from } = useRedirect();
  const dispatch = useAppDispatch();
  const userName = useRef('');
  const password = useRef('');

  if (shouldNavigate) {
    return <Navigate to={from} replace />;
  }

  return (
    <>
      <Container>
        <div className="nb-header">Tuttnauer IoT Login</div>
        <input
          type="text"
          placeholder="User name"
          className={'nb-input-txt p-2 m-2'}
          onChange={(e) => (userName.current = e.target.value)}
        ></input>
        <input
          type="password"
          placeholder="Password"
          className={'nb-input-txt p-2 m-2'}
          onChange={(e) => (password.current = e.target.value)}
        ></input>
        <br />
        <br />
        <div className={'d-flex justify-content-center'}>
          <Button
            className={'nb-submit-button rounded-pill ps-5 pe-5 pt-2 pb-2'}
            onClick={() => dispatch(loginTheUser({ userName: userName.current, password: password.current }))}
          >
            Login
          </Button>
        </div>
      </Container>
    </>
  );
};
