import { useQuery } from '@tanstack/react-query';
import { GET_USER_COMPANY } from 'constants/queryKeys';
import { useSpinnerEffect } from 'hooks/useSpinner';
import { cotService } from 'services/cot.service';
import { Project, ProjectMetadata } from 'types/cot.properties.types';
import { CotDevice, CotProject } from 'types/cot.types';

const createProject = async (cotProject: CotProject, cotDevice: CotDevice | undefined): Promise<Project> => {
  const metadata = await cotService.getProjectProperty<ProjectMetadata>(cotProject.company, cotProject.id, 'meta.data');
  return { metadata, belongToDevice: cotDevice?.project === cotProject.id, ...cotProject };
};

const getFlatProjects = async (companyIds: string[], deviceId: string): Promise<Project[]> => {
  const device = await cotService.getDeviceById(deviceId);
  const projects = await Promise.all(companyIds.map((companyId: string) => cotService.getProjects(companyId)));

  return Promise.all(
    projects.flatMap((project) => project).map(async (cotProject) => await createProject(cotProject, device))
  );
};

export const useGetUserCompaniesQuery = (companyId: string, deviceId: string) => {
  const { data, isLoading } = useQuery([GET_USER_COMPANY], async () => {
    const companies = await cotService.getUserCompanies(companyId);
    const companyIds = companies.map((c) => c.id);
    const flatProjects = await getFlatProjects(companyIds, deviceId);
    return { companies, flatProjects };
  });

  useSpinnerEffect(isLoading);
  return data;
};
