import { Button, Col, Container, Form, Row } from 'react-bootstrap';
import { useNavigate, useParams } from 'react-router';
import { useRef, useEffect } from 'react';
import { GWSetupProgressBar } from '../components/GWSetupProgressBar/GWSetupProgressBar';
import { cotService } from 'services/cot.service';
import { useSpinnerEffect } from 'hooks/useSpinner';
import { useAsync } from 'hooks/useAsync';
import { ClinicDetails } from 'components/ClinicDetails/ClinicDetails';
import { routsConstant } from 'routes/routes.const';
import { useGetProjectData } from 'hooks/project/useGetProjectData';
import { ContactPerson } from 'components/ContactPerson/ContactPerson';
import { GwLocation, projectContactInitValue, ProjectMetadata } from 'types/cot.properties.types';
import { createSearchParams, useSearchParams } from 'react-router-dom';
import { AddNotes } from '../GWSetupNewClinic/AddNotes';
import { AddWorkingHours } from '../GWSetupNewClinic/AddWorkingHours';
import { GwLocationForm } from 'components/GwLocationForm/GwLocationForm';

export const GWSetupSetLocation = () => {
  const [searchParams] = useSearchParams();
  const { projectId, companyId, deviceId } = useParams();
  const navigate = useNavigate();
  const projectMetadataRef = useRef<ProjectMetadata | undefined>(undefined);
  const gwLocationsRef = useRef<GwLocation[] | undefined>(undefined);

  const { status: saveStatus, execute: submit } = useAsync<any>(
    async () =>
      Promise.all([
        cotService.setProjectProperty<GwLocation[]>(companyId!, projectId!, 'gw.location', gwLocationsRef.current!),
        cotService.setProjectProperty<ProjectMetadata>(
          companyId!,
          projectId!,
          'meta.data',
          projectMetadataRef.current!
        ),
      ]),
    { immediate: false }
  );
  const { projectDataStatus, projectMetadata, gwLocations, project } = useGetProjectData(companyId!, projectId!);
  const dataLoadding = [projectDataStatus, saveStatus].includes('pending');
  useSpinnerEffect(dataLoadding);

  useEffect(() => {
    if (saveStatus === 'success') {
      navigate({
        pathname: routsConstant.gwSetupLocationDetails.relatirve({
          companyId: companyId!,
          projectId: projectId!,
          deviceId: deviceId!,
        }),
        search: createSearchParams(searchParams).toString(),
      });
    }
  }, [saveStatus, companyId, projectId, deviceId, navigate, searchParams]);

  if (projectDataStatus === 'pending') {
    return <></>;
  }
  projectMetadataRef.current ??= {
    ...projectMetadata!,
    contacts: projectMetadata?.contacts ?? [],
    contact: projectMetadata?.contact ?? projectContactInitValue(),
  };
  gwLocationsRef.current = gwLocations ?? [{ deviceId: deviceId!, floor: '', room: '' }];
  return (
    <>
      <GWSetupProgressBar currentStep={'Location'} />
      <Container>
        <Form>
          <Row>
            <Col className="setup-step-title mb-4">Set Gateway Location</Col>
          </Row>
          <ClinicDetails projectMetadata={projectMetadataRef.current} project={project}></ClinicDetails>
          <GwLocationForm deviceId={deviceId!} gwLocations={gwLocationsRef.current} />
          <Row>
            <Col>
              <Form.Group>
                <AddNotes projectMetadata={projectMetadataRef.current} />
                <AddWorkingHours projectMetadata={projectMetadataRef.current} />
              </Form.Group>
            </Col>
          </Row>
          <ContactPerson contact={projectMetadataRef.current!.contact!}></ContactPerson>
          <Row className="mt-4 mb-5 text-center">
            <Col>
              <Button
                className="outlined-btn me-3"
                type="button"
                onClick={() =>
                  navigate({
                    pathname: routsConstant.gwSetupClinicSearch.relatirve({
                      deviceId: deviceId!,
                    }),
                    search: createSearchParams(searchParams).toString(),
                  })
                }
              >
                Discard
              </Button>
              <Button className="filled-btn" onClick={() => submit()}>
                Save
              </Button>
            </Col>
          </Row>
        </Form>
      </Container>
    </>
  );
};
